
import autoTable from "jspdf-autotable";
let dataCopy;
export function generateTable(doc, data, superData) {
    doc.addPage();
    console.log(superData);
    dataCopy = data;
    textTable(doc, 'DATOS GENERALES')
    contactInformation(doc, superData.client)
    //Información documental..
    textTable(doc, 'INFORMACIÓN DOCUMENTAL');
    observation(doc, superData.documentaryInformation);
    textTable(doc, 'NO CONFORMIDADES - TABLEROS ELÉCTRICOS');
    tableBoard(doc);
    observation(doc, superData.documentaryInformation);
    //console.log(doc.lastAutoTable.finalY);
    doc.setDrawColor('#17202A');
    doc.setLineWidth(0.6);
    doc.setLineDash([1, 1], 0);
    doc.line(30, doc.lastAutoTable.finalY + 3, 180, doc.lastAutoTable.finalY + 3);
    tableBoard(doc);
    observation(doc, superData.documentaryInformation);

}
function contactInformation(doc, data) {

    const tempData = [
        {
            one:
                "NOMBRE COMERCIAL",
            two: data.tradename,

        },
        {
            one: "MUNICIPIO",
            two: data.municipality,

        },
        {
            one: "FECHA DE INSPECCIÓN ",
            two: data.date,

        },
        {
            one: "CONTACTO",
            two: data.whoAttendedUs,

        }
    ];


    autoTable(doc, {
        theme: 'grid',
        body: tempData,
        columnStyles: {
            one: { cellWidth: 50 },

        },
        columns: [
            { dataKey: "one" },
            { dataKey: "two" },

        ],
        styles: { fontSize: 8, textColor: "#17202A", cellPadding: 0.5, lineColor: "#17202A" },
        margin: { left: 30, right: 30, }
    })
}

///Temporal se tiene que cambiar...
function observation(doc, data) {

    const tempDataTitle = [
        {
            one:
                "OBSERVACIÓN ",
            two: 'SECCIÓN',

        },
    ];

    const tempData = [
        ...tempDataTitle,
        ...data
    ]
    /* suponiento que hay datos en data.. hay que fusionar los datos de información documental */



    autoTable(doc, {
        theme: 'plain',
        body: tempData,

        columns: [
            { dataKey: "one" },
            { dataKey: "two" },

        ],
        styles: { fontSize: 8, textColor: "#17202A", cellPadding: 0.5 },
        margin: { left: 30, right: 30, },
        didParseCell: data => {
            //Desarrollar funcion para poder cambiar de color en diferentes filas..
            if (data.row.index == 0) {
                data.cell.styles.textColor = `#17202A`;
                data.cell.styles.fillColor = `#ffff00`;
            }
        }
    })
}

function tableBoard(doc) {
    const tempData = [
        {
            one: "REFERENCIA",
            two: '1',
        },
        {
            one: "NOMENCLATURA",
            two: 'TAB01',
        },
        {
            one: "LOCALIZACIÓN",
            two: 'ALMACÉN',
        },
    ];

    autoTable(doc, {
        theme: 'plain',
        body: tempData,
        columnStyles: {
            one: { cellWidth: 30 },
        },

        columns: [
            { dataKey: "one" },
            { dataKey: "two" },

        ],
        styles: { fontSize: 8, textColor: "#17202A", cellPadding: 0.5 },
        margin: { left: 35, right: 35, },
    })
}

export function text(doc, size, text, x, y, percent) {
    doc.setFontSize(size);
    if (percent) {
        doc.text(text, x * 6.773, y * 3.81);
    } else {
        doc.text(text, x, y);
    }
}

function textTable(doc, text) {
    autoTable(doc, {
        theme: 'plain',
        body: [[text]],
        styles: { fontSize: 12, textColor: "#17202A", fontStyle: 'bold' },
    })
}

