



export function frontPage(doc, data) {
    doc.setFontSize(22);
    //Imagen..
    imgFront(doc);
    //Titulo..
    ///No mas de 10 caracteres en el nombre comercial..
    //Arreglar este bug..
    doc.text(data.title, 120, 100, { align: 'left', lineHeightFactor: 1.5, maxWidth: 70 });
    //Fecha..
    doc.text(data.date, 120, 200);
}





//Imagen de portada convertida en base 64
//Falta crea un algoritmo para trasnformar las imagenes a base 64 bits.
export function imgFront(doc) {
    doc.text("ANEXO 1", 120, 55);
    const imgData = 'data:image/png;base64,'
    doc.addImage(imgData, 'png', 15, 40, 0, 0);
}