import jsPDF from "jspdf";
import { frontPage } from "./front";
import { transform } from "./adapt_data";
import { generateTable } from "./table";
let dataCopy;
let superData;
let front;
/* Hay que transformar los datos para que se puedan entender correctamente.. */
export function generate(data) {
    console.log(data);
    dataCopy = data
    superData = transform(data);
    front = superData.front;


    create();

    //console.log(transform(data));

    //doc.save("tests.pdf");
}

export function create() {
    const doc = new jsPDF();
    frontPage(doc, front);
    //Se desabilito temporalmente..
    generateTable(doc, dataCopy, superData);
    //window.open(doc.output('bloburl'))
    doc.save(`${dataCopy.tradename.toUpperCase()}- NO CONFORMIDADES TABLEROS ELECTRICOS`);
}